.service {
  padding-top: 90px;
  padding-bottom: 74px;

  background: #1b2d37;
}

.service__title {
  padding: 0;
  margin: 0;
  margin-bottom: 50px;

  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px;
  color: #ffffff;
}

.service__list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
}

.service__item {
  position: relative;

  margin-bottom: 30px;
  margin-right: (30 / 1170) * 100%;
  width: (370 / 1170) * 100%;

  overflow: hidden;

  &:nth-child(3n) {
    margin-right: 0;
  }

  &::after {
    position: absolute;
    content: "";

    bottom: 7px;
    right: 0;

    width: 63px;
    height: 63px;

    background-image: url("../img/icon-button.svg");
    background-repeat: no-repeat;

    pointer-events: none;
  }
}

.service__link {
  display: block;
  padding: 0;
  margin: 0;
  width: 100%;

  text-decoration: none;

  transition-duration: 0.3s;

  &:hover,
  :focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.5;
  }
}

.service__image {
  width: 100%;

  opacity: 0.2;
}

.service__text {
  position: absolute;
  top: 0;
  left: 0;

  padding: 0;
  padding-top: 50px;
  padding-left: 45px;
  padding-right: 20px;
  margin: 0;
  width: 250px;

  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 30px;
  color: #ffffff;
}

.service__button {
  border: none;
  background: transparent;
  outline: none;
}

@media (max-width: $tablet-width) {
  .service {
    padding-top: 50px;
    padding-bottom: 17px;
  }

  .service__wrapper {
    max-width: 765px;
  }

  .service__title {
    margin-bottom: 50px;
    width: 615px;

    font-size: 32px;
    line-height: 140.62%;
  }

  .service__list {
    display: flex;
    flex-wrap: wrap;
  }

  .service__item {
    margin-bottom: 40px;
    margin-right: 0;
    width: 100%;

    &::after {
      position: absolute;
      content: "";

      bottom: 6px;
    }
  }

  .service__image {
    object-fit: cover;
    height: 256px;
  }

  .service__text {
    padding-top: 40px;
    padding-left: 43px;

    &--component {
      padding-top: 50px;
    }
  }
}

@media (max-width: $mobile-width) {
  .service {
    padding-top: 52px;
    padding-bottom: 15px;
  }

  .service__wrapper {
    width: 320px;
  }

  .service__title {
    margin-bottom: 40px;
    padding-left: 10px;
    width: 300px;

    font-size: 28px;
  }

  .service__list {
    display: flex;
    flex-wrap: wrap;
  }

  .service__item {
    margin-bottom: 25px;
  }

  .service__image {
    width: 290px;
    height: 256px;
  }

  .service__text {
    padding-top: 20px;
    padding-left: 35px;
    width: 220px;

    &--component {
      padding-top: 30px;
    }
  }

  .service__button {
    &::after {
      top: 193px;
      left: 227px;
    }
  }
}
